button {
  cursor: pointer;
}

.dryAged {
  padding: 5em 0;
  background-color: #000;
  color: #fff;
  display: flex;

  .daLeft {
    flex: 50%;
    text-align: left;
    align-self: center;
    align-items: flex-end;
    .daTxt {
      display: flex;
      flex-direction: column;
      gap: 3em;
      max-width: 480px;
      margin: auto;
      line-height: 1.5em;
      font-weight: 300;
      .daTxTop {
        h2 {
          font-size: 2em;
          padding-bottom: 0.5em;
          font-weight: 700;
        }

        h4 {
          font-size: 1.4em;
          font-weight: 700;
        }
      }

      .daButtons {
        display: flex;
        flex-direction: row;
        gap: 1em;
        align-items: flex-start;
      }
    }
  }

  .daRight {
    flex: 50%;
    .daIMG {
      width: auto;
      display: flex;
      flex-direction: column;
      gap: 1.1em;
      .botIMG {
        display: flex;
        justify-content: flex-start;
        gap: 1.1em;
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .dryAged {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    padding: 0.5em 0;
    padding-bottom: 5em;
    .daLeft {
      padding-top: 2em;
      text-align: center;

      .daButtons {
        justify-content: center;
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .dryAged {
    align-items: center;
    justify-content: center;

    .daTxt {
      padding: 1em;
      font-size: 0.9em;
    }

    .daRight {
      padding-top: 1em;
      .daIMG {
        text-align: center;
        align-items: center;
        img {
          width: 70%;
        }

        .botIMG {
          width: 70%;
        }
      }
    }
  }
}

@media only screen and (max-width: 375px) {
  .daTxt {
    padding: 1em;
    font-size: 0.7em;
  }
  .daButtons {
    font-size: 0.88em;
  }
}
