.comments {
  background-color: #000;
  color: #fff;
  text-align: center;
  padding-bottom: 8em;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 2em;
  justify-content: center;

  .comHeader {
    h4 {
      color: #555;
      font-size: 1.2em;
      font-weight: 700;
      padding-top: 2em;
      padding-bottom: 1em;
    }

    h2 {
      font-size: 2.5em;
      padding-bottom: 2em;
    }
  }

  .swiper {
    max-width: 1500px;
    margin: 0 auto;
    padding: 10em 0;
    position: relative;
    #rightClick {
      z-index: 100;
      position: absolute;
      top: 48%;
      right: 32%;
      &:hover {
        cursor: pointer;
      }
    }

    #leftClick {
      position: absolute;
      top: 48%;
      z-index: 100;
      left: 32%;
      &:hover {
        cursor: pointer;
      }
    }
    .swiper-wrapper {
      .swiper-slide {
        max-width: 580px;
        margin: 0 auto;
        display: flex;
        gap: 1em;
        justify-content: center;

        .card {
          position: relative;
          font-weight: 300;
          line-height: 24px;
          background-color: #333;
          max-width: 500px;
          padding: 2em 5em;

          .cardText {
            .comment {
              padding-bottom: 5em;
            }
          }

          .cardHead {
            padding-bottom: 1em;
            img {
              display: block;
              margin: 0 auto;
              padding: 0.5em 0;
            }
          }

          .user {
            font-weight: 400;
            font-size: 1em;
          }

          #quote {
            position: absolute;
            bottom: -22px;
            right: 15px;
          }
        }

        .card1 {
          .comment {
            padding-top: 5em;
            padding-bottom: 7em;
          }

          .user {
            padding-bottom: 5em;
          }
        }
      }
    }
  }

  .comFooter {
    padding-top: 2em;
  }
}

@media only screen and (max-width: 1400px) {
  .comments {
    .swiper {
      max-width: 1100px;
      #rightClick {
        right: 25%;
      }

      #leftClick {
        left: 25%;
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .comments {
    .swiper {
      max-width: 950px;

      #rightClick {
        right: 20%;
      }

      #leftClick {
        left: 20%;
      }
    }
  }
}

@media only screen and (max-width: 800px) {
  .comments {
    .swiper {
      max-width: unset;
      margin: unset;
      #rightClick {
        top: 89%;
        right: 35%;
        width: 10%;
      }

      #leftClick {
        top: 89%;
        left: 35%;
        width: 10%;
      }

      #quote {
        display: none;
      }
    }
  }
}

@media only screen and (max-width: 400px) {
  .comments {
    .swiper {
      #rightClick {
        top: 89%;
        right: 25%;
        width: 20%;
      }

      #leftClick {
        top: 89%;
        left: 25%;
        width: 20%;
      }
    }
  }
}
